if(!isMobile()) {
  
  jumpToDesktop();

}else {
  // hideLayout
  let hl = href.match(/hl=[01]/);
  if(hl) {
    hl = hl[0].split('=')[1];

    if(hl == 1) {
      window.localStorage.setItem('hideLayout', 1)
    }else {
      window.localStorage.setItem('hideLayout', 0)
    }
  }

  const InitVue = () => {

    if(isDev()) {
      let host = href.match(/host=\w{0,}/);
      if(host) {
        host = host[0].split('=')[1];
        window.localStorage.setItem('devHost', host)
      }
    }else {
      Vue.use(VueAnalytics, {
        id: 'UA-119925005-4',
        // disableScriptLoader: true, // htmlga
        router, // 
        autoTracking: {
          pageviewOnLoad: false // GApageview
        }
      })
    }

    initSocket();

    Vue.use(VueScrollTo)
    Vue.use(Calendar);

    // register global utility filters.
    Object.keys(filters).forEach(key => {
      Vue.filter(key, filters[key])
    })

    Vue.config.productionTip = false
    
    new Vue({
      router,
      store,
      render: h => h(App),
    }).$mount('#app')

  }

  if(!isApp()) {
    InitVue();
  }else {
    if(href.indexOf('v=') > -1) {
      window.InitVue = InitVue;
    }else {
      InitVue();
    }
  }
}

