import Cookies from 'js-cookie'
import { isApp, isIOS, isAndroid, postMsgToApp, imgFormatter } from '@/utils/index'

function isWebview () {
  var ua = navigator.userAgent;
  // var platform = navigator.platform;
  var chrome = ua.match(/Chrome\/([\d.]+)/) || ua.match(/CriOS\/([\d.]+)/);
  var webview = !chrome && ua.match(/(iPhone|iPod|iPad).*AppleWebKit(?!.*Safari)/);

  return webview;
}

const getAppVersion = (type) => {

  if(window.RNDeviceInfo) {
    return window.RNDeviceInfo.version;
  }

  const href = window.location.href;
  let v = href.match(/v=[^&]+/);

  if(v) {
    v = v[0].split('=')[1];
    if(type == 'number') {
      v = v.replace(/\./g, ''); 
    }
  }

  return v;
};

const LATEST_APP_VERSION = '1.4.0';

const app = {
  state: {
    device: 'mobile',
    size: Cookies.get('size') || 'medium',
    headerHeight: 50,
    singlePageTitle: '',
    activeTab: 1,
    isIOS: isIOS(), //ios
    isAndroid: isAndroid(), //android
    isApp: isApp(),
    hideLayout: (() => {
      if(window.location.href.indexOf('hl=1') > -1) {
        return true;
      }
      if(window.location.href.indexOf('hl=0') > -1) {
        return false;
      }

      return !!Number(window.localStorage.getItem('hideLayout'));
    })(),
    client: 'mobile-' + (isApp() ? 'app' : 'web') + '-' + (isIOS() ? 'ios' : '') + (isAndroid() ? 'android' : ''),
    siteInfo: {},
    appVersion: getAppVersion(),
    appVersionNumber: getAppVersion('number'),
    latestAppVersion: LATEST_APP_VERSION,
    appNeedUpdate: (() => {
      if(isApp()) {
        if(getAppVersion() && LATEST_APP_VERSION) {
          let appVersion = Number(getAppVersion().replace(/\./g, ''));
          let latestAppVersion = Number(LATEST_APP_VERSION.replace(/\./g, ''));
          
          // console.log(latestAppVersion);
          // console.log(appVersion);
    
          if(latestAppVersion > appVersion) {
            return true;
          }
        }else {
          return true;
        }
      }
    
      return false;
    })(),
    domain: {},
    autoExchange: true,
    hightOddsId: 0,
    isDev: process.env.NODE_ENV == 'development',
  },
  mutations: {
    
    SET_SIZE: (state, size) => {
      state.size = size
      Cookies.set('size', size)
    },

    SET_SINGLE_PAEG_TITLE: (state, title) => {
      state.singlePageTitle = title;
    },

    SET_ACTIVE_TAB: (state, tab) => {
      state.activeTab = tab;
    },

    SET_SITE_INFO: (state, data) => {
      state.siteInfo = data
    },

    SET_APP: (state, data) => {
      state.isApp = data
    },

    SET_DOMAIN: (state, domain) => {
      state.domain = domain;
    }
  },
  actions: {

    setSize({ commit }, size) {
      commit('SET_SIZE', size)
    },

    setActiveTab({ commit }, tab) {
      commit('SET_ACTIVE_TAB', tab)
    },

    appSetSinglePageTitle({ commit }, title) {
      commit('SET_SINGLE_PAEG_TITLE', title)
    },

    setSiteInfo({ commit }, data) {
      commit('SET_SITE_INFO', data)
    },

    setAPP({ commit }, data) {
      commit('SET_APP', data)
    },

    postMsgToApp({ commit, state }, data) {
      // if(state)
      return postMsgToApp(data);
    },
    

    setDomain({ commit, state }, domain) {
      commit('SET_DOMAIN', domain);
    },

    appNeedUpdate({ commit }) {
      if(isApp()) {
        if(getAppVersion() && LATEST_APP_VERSION) {
          let appVersion = Number(getAppVersion().replace(/\./g, ''));
          let latestAppVersion = Number(LATEST_APP_VERSION.replace(/\./g, ''));
          
          // console.log(latestAppVersion);
          // console.log(appVersion);
    
          if(latestAppVersion > appVersion) {
            return true;
          }
        }else {
          return true;
        }
      }
    
      return false;
    },
  }
}

export default app
