import { getConfig } from '@/api/config'
import { 
    getBannerAndAnnounceList
} from '@/api/common'

const product = {
  state: {
    config: {},
    announce: {},
    banner: [],
  },

  mutations: {
    SET_CONFIG_LIST: (state, { key, list }) => {
      state.config[key] = list;
    },
    SET_BANNER: (state, { key, list }) => {
      state.config[key] = list;
    },
    SET_ANNOUNCE: (state, { key, list }) => {
      state.announce[key] = list;
    },
  },

  actions: {

    getConfig({ commit, state }, key) {
        if(state.config[key]) {
            return Promise.resolve(state.config[key])
        }

        return getConfig(key).then(json => {
            if(json.code == 200) {
                commit('SET_CONFIG_LIST', { key, list: json.list });

                return json.list;
            }

            return [];
        });
    },
    
    getBannerAndAnnounceList({ commit, state }, key) {
      if(state.announce[key]) {
          return Promise.resolve(state.announce[key])
      }

      return getBannerAndAnnounceList(key).then(json => {
          if(json.code == 200) {
              commit('SET_ANNOUNCE', { key, list: json.list });

              return json.list;
          }

          return [];
      });
    }
  }
}

export default product
