<template>
    <div class="head_info" :style="{ marginTop: (app.isApp ? 0 : 50) + 'px'}">
        <div v-if="!app.hideLayout">

            <div class="box user" v-if="user.roles.indexOf('user') !== -1" :style="{ 'border-color': $route.name == 'index' ? 'transparent' : '#8a8a8a' }">
                <div>
                    <el-button @click="$router.push('/user/exchange')" class="money">
                        <i class="fas"><img src="@/assets/money.png" width="20" /></i>
                        {{ getTotalMoney() }}
                    </el-button>
                </div>
                <span></span>
                <div>
                    <el-button @click="$router.push('/user')" icon="fas fa-user-circle">{{ user.userInfo.username }}</el-button>
                </div>
            </div>
            
            <div class="box try" v-else-if="user.roles.indexOf('try') > -1" :style="{ 'border-color': $route.name == 'index' ? 'transparent' : '#8a8a8a' }">
                <div>
                    <div class="username">{{ user.userInfo.username }}</div>
                </div>
                <span></span>
                <div>
                    <a href="javascript:;" @click="logout"></a>
                </div>
            </div>
            
            <div class="box user" v-else :style="{ 'border-color': $route.name == 'index' ? 'transparent' : '#8a8a8a' }">
                <div>
                    <el-button icon="fas fa-user" @click="authClick('/auth/login')">
                            
                    </el-button>
                </div>
                <span></span>
                <div>
                    <el-button icon="fas fa-user-plus" @click="authClick('/auth/register')">
                        
                    </el-button>
                </div>
            </div>
            <div class="clearfix"></div>
        </div>

    </div>
</template>

<script>

export default {
    name: 'HeaderInfo',
    props: ['app', 'user', 'product'],
    data() {
        return {

        }
    },

    computed: {

    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
        },

        getTotalMoney() {
            let money = Number(this.user.userInfo.total_money);
            
            for(const key in this.product.balance) {
                if(Number(key) !== 0) {
                    money += Number(this.product.balance[key].money);
                }
            }

            return money.toFixed(2);
        },

        authClick(path) {
            if(this.app.isApp) {
                this.$store.dispatch('postMsgToApp', {
                    action: 'presentModal',
                    data: {
                        url: path
                    }
                })
            }else {
			    this.$router.push({ path: path });
            }
        },
        
        logout() {
            this.$store.dispatch('logout').then(() => {
               this.$router.push({ path: this.redirect || '/' })
            })
        }
    }
}
</script>

