<template>
  <div :class="{'app-wrapper': 1,  }" :id="app.siteInfo.yinhe?'yinhe':''" ref="appWrapper" :data-router-name="$route.name">

    <template v-if="!appNeedUpdate">
      <!-- <Header :user="user" :app="app" :product="product" />
      <AppMain :app="app"/>
      <Footer :app="app" /> -->
        <Header :user="user" :app="app" :product="product" v-show="!app.isApp" />
        <HeadInfo v-if="showHeadInfo" :app="app" :user="user" :product="product"/>
        <el-main class="layout_main" ref="layoutMain" :style="{'paddingTop': getPaddingTop(), 'paddingBottom': getPaddingBottom() }">
          <AppMain :app="app" />
        </el-main>
        <Footer :app="app" v-if="app.hideLayout ? false : true" />
    </template>

    <template v-if="appNeedUpdate">
      <el-dialog title="APP" :visible.sync="appNeedUpdate" :fullscreen="true" :show-close="false">
         <p> {{ app.appVersion }}</p>
         <p> {{ app.latestAppVersion }}</p>
         <div>
           <el-button type="primary" @click="appDownload"></el-button>
           <p> <a href="javascript:;">{{ siteLink }}</a> </p>
         </div>

      </el-dialog>
    </template>
  </div>
</template>

<script>
import AppMain from "./components/AppMain";
import Header from "./components/Header";
import Footer from "./components/Footer";
import HeadInfo from "./components/HeadInfo";
import { mapGetters } from "vuex";
import { getDownloadLink } from '@/api/common'

export default {
  name: "Layout",
  components: {
    Header,
    HeadInfo,
    Footer,
    AppMain,
  },

  data() {
    return {
      appNeedUpdate: false,
      siteLink: 'http://' + window.location.host,
      initPostMessage: false,
      showHeadInfo: true,

    }
  },

  computed: mapGetters({
    user: "user",
    app: "app",
    product: "product",
  }),

  mounted() {
    this.init();
  },
  methods: {
    init() {
      if(this.user.roles.indexOf('user') > -1) {
        const sockName = 'tick_' + this.user.userInfo.id;
        window.socket.off(sockName);
        window.socket.on(sockName, msg => {
            this.$store.dispatch('tick').then(() => {
              this.$alert('', '', {
                  confirmButtonText: '',
                  showClose: false,
                  callback: action => {
                      window.location.reload();
                  }
              });
            })
            
        });
        
      }

      if(this.app.isApp) {
        this.$store.dispatch('appNeedUpdate').then(res => {
          if(res) {
            this.appNeedUpdate = true;
          }
        });

        if(!this.initPostMessage) {
          this.initPostMessage = true;
          window.fireMessage = (msg) => {
            // msg = JSON.parse(msg);
            const data = msg.data;
            switch(msg.action) {
              case 'pushPath':
                this.$router.push({ path: data.path });
                break;
              case 'backPath':
                this.$router.back();
                break;
            }
          }
        }
      }

      if(this.$route.name == 'auth-login' || this.$route.name == 'auth-register') {
        // this.showHeadInfo = false;
      }

      if(this.app.isDev) {
          const host = this.$route.query.host;
          if(host) {
              window.localStorage.setItem('devHost', host);
          }
      }
    },

    appDownload() {
      const appVersion = Number(this.app.appVersion.replace(/\./g, ''));

      const t = this.app.isAndroid ? 'android' : 'ios';

      getDownloadLink({
          t: t,
      }).then(json => {
          if(json.code == 200) {
              let url = t == 'android' ? json.list : 'itms-services://?action=download-manifest&url=' + json.list;
              url = json.list;
              this.$store.dispatch('postMsgToApp', {
                  action: 'openURL',
                  data: {
                      url: url,
                  }
              })
          }else {
            alert(json.info);
          }
      })
    },

    getPaddingTop() {
      return 0;

      if(this.app.isApp) {
        return 0;
      }

      // if(this.app.hideLayout) {
      //   return 0;
      // }

      return this.app.headerHeight + 'px';
    },

    getPaddingBottom() {
      // return 0;

      if(this.app.isApp) {
        return 0;
      }

      if(this.app.hideLayout) {
        return 0;
      }

      return 50 + 'px';
    },
  }
}
</script>

<style lang="scss">
.app-wrapper {
  height: 100%;
  // height: calc(100% - 100px);

  &>.el-header {
    padding: 0;
  }

  &>.el-main {
    padding: 0;
  }

  &>.el-footer {
    padding: 0;
  }

  .layout_main {
    // height: 100%;
    padding-bottom: 50px;
  }
}

</style>
