import { 
  loginByUsername, 
  register, 
  getUserInfo, 
  logout, 
  bankcardList, 
  loginAndRegisterByUsername,
  tryPlay
} from '@/api/user'

import { setToken, getToken, removeToken } from '@/utils/auth';
import { isApp, postMsgToApp } from '@/utils/index';
import { Message } from 'element-ui';


const INIT_UESR_INFO = {
  username: "",
  has_draw_passwd: false,
  email: "",
  id: "",
  level: "",
  mobile_phone: "",
  money_type: "",
  name: "",
  total_money: "",
  user_type: "",
}

const user = {
  state: {
    userInfo: Object.assign({}, INIT_UESR_INFO),
    bankcardList: [],
    roles: [],
    token: getToken(),
    domain: {},
  },

  mutations: {
    SET_USER_INFO: (state, user) => {
      if(user.draw_passwd == '0') {
        user.draw_passwd = 0;
      }
      
      for(const key in user) {
        state.userInfo[key] = user[key];
      }
    },

    SET_USER_INFO_BY_KEY: (state, data) => {
      for(let key in data) {
        state.userInfo[key] = data[key];
      }
    }, 

    SET_BANKCARD_LIST: (state, list) => {
      state.bankcardList = list;
    },

    SET_ROLES: (state, roles) => {
      state.roles = roles
    },
    
    SET_DOMAIN: (state, domain) => {
      state.domain = domain;
    }
  },

  actions: {
    // 
    loginByUsername({ commit }, data) {
      return loginByUsername(data.username, data.password).then(json => {
          if(json.code == 200) {
            if(json.token) {
              setToken(json.token);
              if(isApp()) {
                return json;
              }else {
                window.location.reload();
              }
            }else {
              Message.error('');
            }
          }else {
            Message.error(json.info);
          }
          
          return json;
      });
    },

    userRegister({ commit }, data) {
      return register(data).then(json => {
        if(json.code == 200) {
          if(json.token) {
            setToken(json.token);
            if(isApp()) {
              return json;
            }else {
              window.location.reload();
            }
          }else {
            Message.error('');
          }
        }else {
          Message.error(json.info);
        }
        
        return json;

      });
    },

    loginAndRegisterByUsername({ commit }, data) {
      return loginAndRegisterByUsername(data).then(json => {
          if(json.code == 200) {
            if(json.token) {
              setToken(json.token);
              if(isApp()) {
                return json;
              }else {
                if(!data._stopReload) {
                  window.location.reload();
                }
              }
            }else {
              Message.error('');
            }
          }else {
            Message.error(json.info);
          }
          
          return json;
      });
    },

    // 
    getUserInfo({ commit }) {
      return getUserInfo().then(json => {
        if(json.code == 200) {
          commit('SET_ROLES', json.roles);
          commit('SET_USER_INFO', json.list)
        }

        return json;
      });
    },

    setUserInfo({ commit, state }, data) {
      const userInfo = Object.assign(state.userInfo, data.info);
      commit('SET_USER_INFO', userInfo);
      commit('SET_ROLES', data.roles);

      return data;
    },

    updateUserInfo({ commit, state }, data) {
      const userInfo = Object.assign({}, state.userInfo, data);
      commit('SET_USER_INFO', userInfo);

      return data;
    },

    setDomain({ commit, state }, domain) {
      commit('SET_DOMAIN', domain);
    },

    logout({ commit }) {
      removeToken();
      commit('SET_USER_INFO', INIT_UESR_INFO);
      commit('SET_ROLES', ['guest']);

      if(isApp()) {
        postMsgToApp({
          action: 'logout',
        })
        return ;
      }
      
      // window.location.reload();
      // return ;

      return logout().then((json) => {
        if(json.code == 200) {
          window.location.reload();
          return ;
          
          commit('SET_USER_INFO', INIT_UESR_INFO);
          commit('SET_ROLES', ['guest']);
        }

        return json;
      });
    },

    tick({ commit }) {
      removeToken();
      commit('SET_USER_INFO', INIT_UESR_INFO);
      commit('SET_ROLES', ['guest']);

      if(isApp()) {
        postMsgToApp({
          action: 'logout',
        })
      }

      return ;
      
      return logout().then((json) => {
        if(json.code == 200) {
          commit('SET_USER_INFO', INIT_UESR_INFO);
          commit('SET_ROLES', ['guest']);
        }

        return json;
      });
    },

    bankcardList({ commit }) {
      return bankcardList().then((json) => {
        if(json.code == 200) {
          commit('SET_BANKCARD_LIST', json.list);
        }

        return json;
      });
    },
    
    tryPlay({ commit, state }, data) {
       return tryPlay().then(json => {
         
        commit('SET_ROLES', json.roles);
        
        if(json.code == 200) {
          commit('SET_USER_INFO', json.list);
          window.location.reload();
        }

        return json;
      });
    },
    

    // 
    // LoginByThirdparty({ commit, state }, code) {
    //   return new Promise((resolve, reject) => {
    //     commit('SET_CODE', code)
    //     loginByThirdparty(state.status, state.email, state.code).then(response => {
    //       commit('SET_TOKEN', response.data.token)
    //       setToken(response.data.token)
    //       resolve()
    //     }).catch(error => {
    //       reject(error)
    //     })
    //   })
    // },

    // 
    // LogOut({ commit, state }) {
    //   return new Promise((resolve, reject) => {
    //     logout(state.token).then(() => {
    //       commit('SET_TOKEN', '')
    //       commit('SET_ROLES', [])
    //       removeToken()
    //       resolve()
    //     }).catch(error => {
    //       reject(error)
    //     })
    //   })
    // },

    // //  
    // FedLogOut({ commit }) {
    //   return new Promise(resolve => {
    //     commit('SET_TOKEN', '')
    //     removeToken()
    //     resolve()
    //   })
    // },

    // // 
    // ChangeRoles({ commit, dispatch }, role) {
    //   return new Promise(resolve => {
    //     commit('SET_TOKEN', role)
    //     setToken(role)
    //     getUserInfo(role).then(response => {
    //       const data = response.data
    //       commit('SET_ROLES', data.roles)
    //       commit('SET_NAME', data.name)
    //       commit('SET_AVATAR', data.avatar)
    //       commit('SET_INTRODUCTION', data.introduction)
    //       dispatch('GenerateRoutes', data) //  
    //       resolve()
    //     })
    //   })
    // }
  }
}

export default user
