const request = {
  get(url, data) {
    data = data || {}
    data.client = store.getters.app.client;
    return service({
        url: url,
        method: 'get',
        params: data,
    }).then(response => {
        return response.data;
    });
  },
  
  post(url, data) {
    data = data || {}
    data.client = store.getters.app.client;

    return service({
        url: url,
        method: 'post',
        data: data,
    }).then(response => {
        return response.data;
    });
  }
};

export default request
