<template>
    <div id="footer" v-if="!app.isApp">
        <el-row :gutter="0">
            <el-col style="width: 20%;" :span="6" v-for="item in routes" :key="item.index">
                <div class="grid-content bg-purple" :class="{ focused: item.focused }" @click="changeTab(item)">
                    <div class="tab_item">
                        <i :class="item.webIcon"></i>
                        {{ item.label }}
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { imgFormatter } from '@/utils/index' 

export default {
    name: 'Footer',
    props: ['app'],

    data() {
        const routes = [
            { index: 1, label: '', title: this.app.siteInfo.name, path: '/', focused: true, icon: 'home', webIcon: 'fas fa-home' },
            { index: 2, label: '', title: '', path: '/charge', focused: false, icon: 'money-collect', webIcon: 'fas fa-yen-sign' },
            { index: 3, label: '', title: '', path: '/activity', focused: false, icon: 'fire', webIcon: 'fas fa-fire-alt' },
            { index: 4, label: '', title: '', path: '/help', focused: false, icon: 'customer-service', webIcon: 'fas fa-comment-dots' },
            { index: 5, label: '', title: '', path: '/user', focused: false, icon: 'user', webIcon: 'fas fa-user' },
        ];
        
        if(this.app.isApp) {
            if(this.app.appVersionNumber > 130) {
                routes[0].title = imgFormatter(this.app.siteInfo.logo);   
            }
        }
        
        return {
            focusedTab: 1,
            routes,
        }
    },

    watch: {
        '$route'(to, from) {
            this.setTabByRoute(to);
        }
    },

    computed: {

    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            if(this.app.isApp) {
                this.$store.dispatch('postMsgToApp', {
                    action: 'setTab',
                    data: this.routes
                });
            }

            this.setTabByRoute(this.$route);
        },

        setTabByRoute(route) {
            let focusedTabIndex = 1;

            const routeName = route.name;
            if(routeName == 'user-page') {
                focusedTabIndex = 5;
            }else if(routeName == 'help-index') {
                focusedTabIndex = 4;
            }else if(routeName.indexOf('activity-') == 0) {
                focusedTabIndex = 3;
            }else if(routeName == 'charge-index') {
                focusedTabIndex = 2;
            }else if(routeName == 'index') {
                focusedTabIndex = 1;
            }else if(routeName.indexOf('auth-') > -1) {
                focusedTabIndex = 0;
            }

            this.routes.forEach(route => {
                if(route.index == focusedTabIndex) {
                    route.focused = true;
                }else {
                    route.focused = false;
                }
            })

            // if(this.focusedTab != focusedTab) {
            //     this.focusedTab = focusedTab;
            // }
        },

        changeTab(route) {
            this.$router.push({ path: route.path });
        }
    }
}
</script>

