<template>
    <header id="partials_header" :style="{'height': app.headerHeight + 'px' }">
        <div class="fl_left">
            <!-- <span v-if="app.hideLayout">
                <a href="javascript:;" class="bar"><i class="fas fa-bars"></i></a>&nbsp;
            </span> -->
            
            <div v-if="leftType == 'menu'">
                <!-- <a href="javascript:;" class="bar"><i class="fas fa-bars"></i></a> -->
                <router-link to="/" :class="{'logo': 1 }" v-if="showLogo">
                    <img v-if="app.siteInfo.logo" :src="app.siteInfo.logo | imgFormatter" />
                </router-link>
            </div>
            <span v-if="leftType == 'back'">
                <a href="javascript:;" @click="back" class="back"><i class="fas fa-chevron-left"></i> </a>
            </span>

            <span v-if="leftType == 'toQzIndex'">
                <router-link to="/qz"><i class="fas fa-home"></i></router-link>
            </span>

            <span v-if="leftType == 'hideLayout'">
                <!--<router-link to="/android" target="_blank" v-if="app.isAndroid">APP</router-link>-->
                <!--<router-link to="/ios" v-if="app.isIOS">APP</router-link>-->
                <router-link to="/app" target="_blank">APP</router-link>
            </span>
        </div>

        <div class="title" v-if="title">
            <span v-html="title"></span>
            <!-- <router-link to="/" class="logo" v-if="showLogo">
                <img v-if="app.siteInfo.logo" :src="app.siteInfo.logo | imgFormatter" />
            </router-link> -->
        </div>

        <div class="fl_right">
            <span v-if="!title">
                <!-- <span v-if="user.roles.indexOf('user') == -1">
                    <el-button type="success"><router-link to="/auth/login"></router-link></el-button>
                    <el-button type="warning"><router-link to="/auth/register"></router-link></el-button>
                </span> -->

                <span v-if="user.roles.indexOf('user') > -1" class="user">
                    <!-- <router-link to="/user" v-if="user.userInfo.username">{{ user.userInfo.username }}</router-link> -->
                    <!-- <router-link to="/user" v-if="!app.hideLayout" class="m">{{ getTotalMoney() }}</router-link> -->
                    <a href="javascript:;" v-if="app.hideLayout" class="m">{{ getTotalMoney() }}</a>
                </span>
                <span class="try_play" v-if="!app.isApp && user.roles.indexOf('user') == -1 && user.roles.indexOf('try') == -1">
                    <a href="javascript:;" @click="tryPlay" ></a>
                </span>
                <span class="app_download" v-if="!app.isApp && !app.hideLayout">
                    <!--<router-link to="/android" target="_blank" v-if="app.isAndroid">APP</router-link>-->
                    <!--<router-link to="/ios" v-if="app.isIOS">APP</router-link>-->
                    <router-link to="/app" target="_blank"  v-if="user.domain.agent_id != 58150">APP</router-link>
                </span>
            </span>
            <div v-if="title">
                <span v-if="app.hideLayout && user.roles.indexOf('user') != -1" class="user">
                    <a href="javascript:;" class="m">{{ getTotalMoney() }}</a>&nbsp;
                    <router-link to="/charge" v-if="$route.name != 'charge-index'"></router-link>&nbsp;

                    <span v-if="$route.name == 'Qz'">
                        <router-link to="/charge"></router-link>&nbsp;
                        <router-link to="/user/withdraw"></router-link>
                    </span>
                    
                    <router-link to="/user/transfer" v-if="$route.name == 'charge-index'"></router-link>
                
                </span>
                <el-button v-else type="text"><router-link to="/"><i class="fas fa-home"></i></router-link></el-button>
            </div>
        </div>
    </header>
</template>

<script>
import { mapGetters } from 'vuex'
import { imgFormatter } from '@/utils/index';

export default {
    name: 'Header',
    props: ['user', 'app', 'product'],
    components: {

    },

    data() {
        return {
           title: '',
           leftType: 'menu',
           showLogo: false,
           timer: null,
           updateTitleTimer: null,
           androidDownloadAddr: '',
        }
    },

    watch: {
        '$route'(to, from) {
            this.setTitleByRoute(to);
        }
    },

    computed: {

        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && checkNavIsShow(item.id);
            });

            return list;
        },

		product() { return this.$store.getters.product },
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            this.setTitleByRoute(this.$route);

            if(this.user.roles.indexOf('user') > -1) {
                this.reloadBalance();
            }
        },

        reloadBalance() {
            const run = () => {
                if(this.user.roles.indexOf('user') > -1) {
                    const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
                    this.$store.dispatch('productBalance', { product_ids })
                }
            }

            run();

            clearInterval(this.timer);

            let timeSec = 1000 * 30;
            if(this.app.hideLayout) {
                timeSec = 1000 * 10;
            }

            this.timer = setInterval(run, timeSec);
        },

        setTitleByRoute(route) {
            let title = '', leftType = 'menu', showLogo = false;
            const routeName = route.name;

            // console.log(routeName);

            if(routeName == 'user-page') {
                const page = route.params.page;

                if(!page) {
                    title = '';
                    leftType = 'menu';
                } else {
                    leftType = 'back';

                    if(page == 'transfer') {
                        title = '';
                    }else if(page == 'bank') {
                        title = '';
                    }else if(page == 'security') {
                        title = '';
                    }else if(page == 'withdraw') {
                        title = '';
                    }else if(page == 'exchange') {
                        title = '';
                    }else if(page == 'order') {
                        title = '';
                    }
                }

                if(this.app.hideLayout) {
                    leftType = 'toQzIndex';
                }
                
            }else if(routeName == 'help-index') {
                title = '';
                leftType = 'back';

                if(this.app.hideLayout) {
                    leftType = 'toQzIndex';
                }

            }else if(routeName == 'vip-index') {
                title = 'VIP';
                leftType = 'back';
            }else if(routeName == 'activity-index') {
                title = '';
                leftType = 'menu';

                if(this.app.hideLayout) {
                    leftType = 'toQzIndex';
                }

            }else if(routeName == 'activity-detail') {
                title = '';
                leftType = 'back';

                if(this.app.hideLayout) {
                    leftType = 'toQzIndex';
                }

            }else if(routeName == 'charge-index') {
                title = '';
                leftType = 'menu';

                if(this.app.hideLayout) {
                    leftType = 'toQzIndex';
                }
            }else if(routeName == 'product-index') {
                title = '';

                const pid = this.$route.params.productId;
                if(pid) {
                    this.product.list.forEach(item => {
                        if(pid == item.id) {
                            title = item.name;
                        }
                    })
                }
                
                leftType = 'back';

                if(this.app.hideLayout) {
                    leftType = 'hideLayout';
                }

            }else if(routeName == 'index') {
                title = '';
                leftType = 'menu';
                showLogo = true;

                if(this.app.isApp) {
                    title = this.app.siteInfo.name;
                    
                    if(this.app.appVersionNumber > 130) {
                        title = imgFormatter(this.app.siteInfo.logo);   
                    }
                }

            }else if(routeName == 'ios') {
                title = 'IOS';
                showLogo = false;

                if(this.app.hideLayout) {
                    leftType = 'toQzIndex';
                }
            }else if(routeName == 'auth-login') {
                if(this.app.isApp) {
                    title = '';
                }
                showLogo = true;
            }else if(routeName.indexOf('auth-register')>-1) {
                if(this.app.isApp) {
                    title = '';
                }
                showLogo = true;
            }else if(routeName == 'Qz') {
                if(this.app.hideLayout) {
                    leftType = 'toQzIndex';
                }

                title = '';
            }else if(routeName == 'Nav') {
                // product
                const id = this.$route.params.id;
                title = '';

                this.product.navList.forEach(item => {
                    if(item.id == id) {
                        title = item.name;
                    }
                })

                leftType = 'back';
            }

            if(this.title != title) {
                this.title = title;
            }

            if(this.leftType != leftType) {
                this.leftType = leftType;
            }

            if(this.showLogo != showLogo) {
                this.showLogo = showLogo;
            }
            
            if(this.app.isApp) {
                this.$nextTick(() => {
                    this.$store.dispatch('postMsgToApp', {
                        action: 'routeChange',
                        data: {
                            title,
                            leftType,
                            path: route.path,
                        }
                    })
                })
            }
        },

        back() {
            const routePath = this.$route.path;

            if(routePath.indexOf('/user') == 0) {
                this.$router.push({ path: '/user' });
            }else {
                this.$router.go(-1);
            }

        },

        getTotalMoney() {
            let money = Number(this.user.userInfo.total_money);
            
            for(const key in this.product.balance) {
                if(Number(key) !== 0) {
                    money += Number(this.product.balance[key].money);
                }
            }

            return money.toFixed(2);
        },
        
        
        tryPlay() {
            this.$store.dispatch('tryPlay');
        },
    }
}
</script>

