<template>
  <section class="app-main" ref="appMain">
    <keep-alive :include="cachedViews">
      <router-view :key="key"/>
    </keep-alive>
  </section>
</template>

<script>

export default {
  name: 'AppMain',
  props: ['app'],
  computed: {
    cachedViews() {
      return this.$store.state.tagsView.cachedViews
    },
    key() {
      return this.$route.fullPath
    },
  },

  watch: {
      '$route'(to, from) {
          this.setScroll(to);
      }
  },

  mounted() {
    this.setScroll(this.$route);
  },

  methods: {
    setScroll(to) {
      return ;
      
      if(to.name == 'product-index') {
        return ;
      }

      this.$nextTick(() => {
        let name = '.app-main';
        const selector = window.document.querySelector(name);
        if(selector) {
          this.scroll = new BScroll(selector, {
            click: true,
          })
        }
      })
    }, 
  }
}
</script>

<style scoped>
.app-main {
  /* height: 100%; */
}
</style>

